        console.error("数据查询错误");
      }
    });
}
notPassupdatedoctor(){
          let a = this.hospitallist.filter(item => {return (item.hospitalId == this.formLabelAlign1.hospitalId);});
          this.loading = true
          let hospital = []
          console.log('1111',a)
